<template>
  <div class="page-title" v-if="Object.keys(goods).length !== 0">
       <section class="page-warp">
            <div class="page-cell">{{ goods.title }}</div>
       </section>
       <section class="page-title-top">
           <div class="title-price">
               <span class="price-yan">
                    <!-- <i class="dol">¥</i> -->
                    <span class="price">{{ goods.newPrice }}</span>
               </span>
               <span class="price-del">{{ goods.oldPrice }}</span>
               <span class="price-icon"  v-if="goods.discount"> {{ goods.discount }}</span>       
            </div>
       </section>
        <section class="page-adds page-x">
                 <span class="kd">{{ goods.columns[0] }}</span>
                 <span class="yx">{{ goods.columns[1] }}</span>
                 <span class="df">{{ goods.services[goods.services.length - 1].name }}</span>
       </section>
       <section class="page-adds">
            <span v-for="index in goods.services.length-1" :key="index">
                <img  :src="goods.services[index-1].icon" v-show="goods.services[index-1].icon"><span>{{goods.services[index-1].name}}</span>
            </span>
       </section>

  </div>
</template>

<script>
export default {
  name: "PageTitle",
  props: {
    goods: {
      type: Object,
      default() {
        return {};
      }
    }
  }
 
};
</script>
<style lang="scss" scoped>
.page-title{
    border-bottom:5px solid #f2f5f8;
    .page-title-top{
      background-color: #FFF;
      padding: 0 10px;
      .title-price{
        color: #FF0036;
        height: 36px;
        line-height: 36px;
        .price-yan{
            .dol{
                font-family: arial;
                font-style: normal;
                font-size: 18px;
                position: relative;
                top: 2px;
            }
            .price{
                font-size: 24px;
                position: relative;
                top: 2px;
                margin-right: 5px;
            }
        }
        .price-del{
            color: #999;
            font-size: 12px;
            padding: 1px 2px;
            text-decoration:line-through;
        }
        .price-icon{
            background: -webkit-linear-gradient(left,#ff6034,#ee0a24);
            background: linear-gradient(90deg,#ff6034,#ee0a24);
            color: #FFF;
            font-size: 12px;
            padding: 2px 8px;
            border-radius: 30px;
        }
      }
    }
    .page-warp{
         padding: 5px 10px 0;
         box-sizing: border-box;
         .page-cell{
            font-size: 14px;
            color: #051B28;
            line-height: 20px;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            max-height: 63px;
            -webkit-box-orient: vertical;
            -webkit-box-pack: center;
            overflow: hidden;
            word-break: break-all;
            flex: 1;
         }
    }
    .page-x{
       border-bottom: 1px solid hsla(0,0%,39.2%,.1);
    }
    .page-adds{
        display: -webkit-box;
        display: flex;
        background-color: #FFF;
        line-height:1.05;
        padding: 15px 10px;
        font-size: 12px;
        color: #999;
        justify-content: space-between;
        span:first-child {
            text-align: left;
        }
        span:last-child {
            text-align: right;
        }
        span{
            display: flex;
            -webkit-box-align: center;
            -webkit-align-items: center;
            -ms-flex-align: center;
            align-items: center;
            img{
                display: inline-block;
                width: 16px;
                height: 16px;
            }
        }
    }
}
</style>