<template>
<div class="page-detail">
   <!--头部搜索-->
    <header class="page-header-g">
            <head-menu class="home-header">
            <i class="iconfont icon-back" slot="left" @click="backHomePage"></i>
            <tab-menu :tabmenulist="pagetitle"  class="page-color" slot="title"></tab-menu>
            </head-menu>
    </header>
    <!-- <ul>
        <li v-for="(item, index) in $store.state.cartlist" :key="index">{{item}}</li>
    </ul> -->
    <!--滚动插件-->
    <my-scroll ref="scroll">
            <!--幻灯片-->
            <page-swiper :bannerlist="topImages" class="detail-set-scroll"></page-swiper>
            <!--头部信息-->
            <page-title :goods="goods"></page-title>
            <!--店铺信息-->
            <page-store :shop="shops"></page-store>
            <!--店铺图片展示信息-->
            <page-image :images-info="detailsInfo"></page-image>
            <!--尺寸展示信息-->
            <page-size :param-info="paramsInfo"></page-size>
            <!--留言信息-->
            <page-say :comment-info="commentInfo"></page-say>
            <!--推荐列表-->
            <goods-list :goods="recommendList"></goods-list>
      </my-scroll>
       <!--添加购物车-->
      <buy-now @addCart="addToCart"></buy-now>
</div>
</template>

<script>
//内部引用
import PageSwiper from "views/page/modular/PageSwiper";
import PageTitle from "views/page/modular/PageTitle";
import PageStore from "views/page/modular/PageStore";
import PageImage from "views/page/modular/PageImage";
import PageSize from "views/page/modular/PageSize";
import PageSay from "views/page/modular/PageSay";


//外部组件
import HeadMenu from "components/common/headmenu/HeadMenu";
import TabMenu from "components/common/tabmenu/TabMenu";
import MyScroll from "components/common/scroll/MyScroll";
import GoodsList from "components/content/goods/GoodsList";
import BuyNow from "components/content/buy/BuyNow";

import { getProductDetail, Goods, Shop, GoodsParams, getRecommend } from "@/network/detail";
import {imgListenerMixin} from '@/commonjs/mixin';
export default {
  name: "PageDetail",
  mixins: [imgListenerMixin],
  components:{
      MyScroll,
      HeadMenu,
      TabMenu,
      PageSwiper,
      PageTitle,
      PageStore,
      PageImage,
      PageSize,
      PageSay,
      GoodsList,
      BuyNow
  },
  data(){
      return{
           detailId:"",
           pagetitle:["商品","参数","评论","推荐"],
           topImages: [],
           goods: {},
           shops: {},
           detailsInfo:{},
           paramsInfo:{},
           commentInfo:{},
           recommendList: [],
      }
  },
  created() {
    // 获取页面传过来的id
    this.detailId = this.$route.params.iid;

    // 发送网络请求
    this.getProductDetail();
    this.getRecommend();
  },
  beforeCreate() {
    // 进入详情页隐藏tabBar
    this.$store.commit("setTabBarShow", false);
  },
   // 路由离开的时候恢复tabBar
  beforeRouteLeave(to, from, next) {
    this.$store.commit("setTabBarShow", true);
    next();
  },
  methods:{
      //数据请求操作-----------------------------------------------------------------------------------------------------------------------------
      getProductDetail() {
        getProductDetail(this.detailId).then(res => {
            const data = res.result;
            console.log(data);
             // 获取轮播图数据
             this.topImages = data.itemInfo.topImages;
             // 获取商品数据,调用封装的ES6的class
             this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services);
             // 获取店铺数据
             this.shops = new Shop(data.shopInfo);
             // 获取下面的图片展示数据
             this.detailsInfo = data.detailInfo;
             // 获取尺寸数据
             this.paramsInfo = new GoodsParams(data.itemParams.info, data.itemParams.rule || {});
            // 获取评论数据
            if (data.rate.cRate !== 0) {
            this.commentInfo = data.rate.list[0] || {};
            }
        })
      },
      
      getRecommend() {
            getRecommend().then(res => {
                // console.log(res);
                this.recommendList = res.data.list;
            });
      },

      //结束数据操作-----------------------------------------------------------------------------------------------------------------------------

      //DOM操作-----------------------------------------------------------------------------------------------------------------------------
      //返回上一页
      backHomePage(){
         this.$router.go(-1);
      },
      //添加到购物车数据
      addToCart(){
            // 1.获取需要展示到购物车的商品信息
             const obj = {
                image: this.topImages[0],
                title: this.goods.title,
                desc: this.goods.desc,
                price: this.goods.lowNowPrice,
                id: this.detailId  //iid是必须传的才知道是那个页面
            }
            // 2.将商品数据传到购物车页面

            //this.$store.cartlist.push(obj) 不建议直接在store保存数据
            // this.$store.commit('setCartToData', obj)  //commit方法---在mutations.js方法里面操作数据
            this.$store.dispatch("setCartToData", obj);  //dispatch方法---在actions.js方法里面操作

      },
      //用总线更新图片滚动条
      imgLoad() {
           console.log('123');
          this.$refs.scroll && this.$refs.scroll.update();
          // 图片加载完获取classList
          this.getClassList();
      },
      // 获取classList
        getClassList() {
        // Array.from() 将伪数组转换成纯数组
        this.detailClassList = [];
        this.detailClassList = Array.from(document.getElementsByClassName("detail-set-scroll"));
        let maxValue = 10000000;
        this.detailClassList.push({ offsetTop: maxValue });
        console.log(this.detailClassList);
        },

  }

};
</script>
<style lang="scss" scoped>
.page-detail{
    height: calc(100vh -50px);
    .page-header-g{
        background: linear-gradient(90deg,#27a6fa,#8e71f5);
        background: -webkit-linear-gradient(left,#27a6fa,#8e71f5);
        .icon-back{
            color: #fff;
        }
    }
    .page-color{
        color:#fff;
        width:260px;
        ::v-deep .active{
            color:#fff;
            span{
                border-bottom: 3px solid #fff;
            }
        }
    }
}

</style>